.logo-item {
  opacity: 0; }

#Logo {
  align-self: center;
  width: 600px; }

.logo-x {
  opacity: 1;
  animation-name: logo-x;
  animation-duration: 2.2s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  transform-origin: center;
  animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  backface-visibility: hidden; }

.logo-b {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 0.13061s; }

.logo-o {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 0.57959s; }

.logo-w {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 0.94694s; }

.logo-l {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 1.23265s; }

.logo-i {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 1.43673s; }

.logo-n {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 1.55918s; }

.logo-g {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-delay: 1.6s; }

@-webkit-keyframes logo-x {
  from {
    transform: translate3d(-600px, 0, 0) rotate3d(0, 0, 1, -720deg); }

  to {
    transform: translate3d(0, 0, 0) rotate3d(0, 0, 0, 0deg); } }

